:root {
  --black-color: #000;
  --white-color: #fff;
  --bg-color: #ae1332;
  --text-shadow: 0 4px 70px #0000001a;
  --letter-spacing: -.04em;
  --letter-spacing-smaller: -.02em;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--white-color);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  color: currentColor;
  font-family: inherit;
  line-height: inherit;
  border: none;
}

input, textarea {
  font-family: inherit;
  line-height: inherit;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

.is-closed {
  display: none;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
}

.is-visible {
  visibility: visible;
  opacity: 1;
}

@media screen and (width >= 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    max-width: 1280px;
  }
}

.header {
  background-color: var(--color-white);
  max-width: 100%;
}

.header .header-wrap {
  align-items: center;
  display: flex;
}

.header .nav-layout {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 16px 0;
  display: flex;
}

.nav-layout .site-nav {
  list-style-type: none;
  display: none;
}

.nav-layout .logo-box {
  gap: 8px;
  display: flex;
}

.nav-layout .logo-text {
  text-transform: uppercase;
  color: var(--bg-color);
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.header-wrap .icon-basket {
  display: none;
}

.off-screen-menu .icon-wrap {
  margin-bottom: 190px;
}

.off-screen-menu {
  background-color: var(--bg-color);
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  width: 100%;
  height: 100vh;
  padding-top: 124px;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 50px;
  right: -1280px;
}

.off-screen-menu.active {
  z-index: 99;
  right: 0;
}

.ham-menu {
  cursor: pointer;
  width: 20px;
  height: 24px;
  position: relative;
}

.ham-menu span {
  background-color: var(--bg-color);
  border-radius: 25px;
  width: 100%;
  height: 2px;
  transition: all .3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ham-menu span:first-child {
  width: 50%;
  top: 25%;
  left: 5px;
}

.ham-menu span:nth-child(3) {
  width: 75%;
  top: 75%;
  left: 7.5px;
}

.ham-menu.active span:first-child {
  width: 100%;
  top: 50%;
  transform: translate(-50%, -50%)rotate(45deg);
}

.ham-menu.active span:nth-child(2) {
  opacity: 0;
}

.ham-menu.active span:nth-child(3) {
  width: 100%;
  top: 50%;
  transform: translate(-60%, -50%)rotate(-45deg);
}

.site-nav {
  display: flex;
}

.off-screen-menu .link {
  letter-spacing: var(--letter-spacing);
  text-align: center;
  color: var(--color-white);
  line-height: 129%;
}

.logo-text {
  text-transform: uppercase;
  text-align: center;
  color: var(--color-red);
  align-items: center;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

@media screen and (width <= 767px) {
  .header-wrap .icon-basket, .button-header {
    display: none;
  }

  .header .container {
    max-width: 767px;
  }
}

